// import moment from 'moment'
import {
  get,
  post,
  axiosConfig,
  postJson
} from '@/config/axios.config';
import {
  cloneDeep
} from 'lodash';
import moment from 'moment';
export const pagerMixin = {
  activated() {
    this.$nextTick(() => {
      this.z_method.refreshSpread()
    })
  },
  data() {
    return {
      // 翻页
      pagination: {
        currentPage: 1,
        total: 0,
        pageSize: 100,
      }, // 分页参数
      optionsClone: {},
      options: {},
      // 导出
      btnDisabled: false, // 控制是否能点击按钮
      exportObj: {
        visible: false,
        sum: 1, //一共获取数据的总次数
        current: 0, //当前获取第几次数据
        fileName: '导出文件名',
        excelData: [],
      },
      // 导出请求方式，请求链接，请求条数
      requestMode: 'post',
      requestUrl: '/',
      requestPageSize: 5000,
      // 勾选下载数据 或但也下载数据
      checklistData: [],
      isLayering: false, // 分页和参数是否分开传
      isQs: true, // 是否按照表单传值方式， false是json传值
      billsCode: '',
      sbc: '',
    };
  },
  mounted() {},
  methods: {
    query() {
      console.log('000000');
      // throw new Error('component must implement query method')
    },
    // 页数变化和当前页变化
    fetchData(val) {
      this.pagination = {
        ...this.pagination,
        pageSize: val.pageSize ? val.pageSize : this.pagination.pageSize,
        currentPage: val.page ? val.page : 1,
      };
      // if (val.page) {
      //   this.pagination.currentPage = val.page
      // }
      let options = {};
      if (JSON.stringify(this.optionsClone) == '{}') {
        this.msg('请先查询');
        return;
      }
      if (this.isLayering) {
        options = {
          ...this.optionsClone,
          pageParam: {
            pageSize: this.pagination.pageSize,
            currentPage: this.pagination.currentPage,
          },
        };
      } else {
        options = {
          ...this.optionsClone,
          pageSize: this.pagination.pageSize,
          currentPage: this.pagination.currentPage,
        };
      }
      this.query(options);
    },
    // 导出
    exportCsv(_this) {
      _this.exportObj.visible = true; //显示msg框
    },
    closeDialog(val) {
      this.exportObj.visible = val;
      this.exportObj.current = 1;
    },
    searchExport() {
      this.btnDisabled = true;
      if (this.tableData.length === 0) {
        this.msg('没有数据无法导出！');
        this.btnDisabled = false;
        return;
      }
      let options = cloneDeep(this.optionsClone);
      // this.searchExportIn(this.pagination.total, options);
      if (this.pagination.total > 200000) {
        this.msg('导出失败，本次数据超过20W条，请分批导出！');
        this.btnDisabled = false;
        return;
      }
      let num = Math.ceil(this.pagination.total / this.requestPageSize);
      // 需要导出多少次
      this.exportObj.sum = num;
      this.exportObj.current = 0;
      this.exportSync(0, num, options, this.requestUrl);
    },
    exportSync(currentPage, num, options, url) {
      if (currentPage == num) {
        this.btnDisabled = false;
        // 当页数等于总次数，则结束循环
        return;
      }
      ++currentPage;
      // 区别后端传值的方式
      if (this.isLayering) {
        options.pageParam.pageSize = this.requestPageSize;
        options.pageParam.currentPage = currentPage;
      } else {
        options.pageSize = this.requestPageSize;
        options.currentPage = currentPage;
      }
      if (this.requestMode == 'post') {
        this.exportPost(num, currentPage, options, url);
      } else {
        this.exportGet(num, currentPage, options, url);
      }
    },
    // post导出
    exportPost(num, currentPage, options, url) {
      if (this.isQs) {
        post(url, options, this.isQs)
          .then(res => {
            this.exportNormal(res, num, currentPage, options, url);
          })
          .catch(res => {
            this.exportError(num);
          });
      } else {
        postJson(url, options)
          .then(res => {
            this.exportNormal(res, num, currentPage, options, url);
          })
          .catch(res => {
            this.exportError(num);
          });
      }
    },
    // get的导出
    exportGet(num, currentPage, options, url) {
      get(url, options, this.isQs)
        .then(res => {
          this.exportNormal(res, num, currentPage, options, url);
        })
        .catch(res => {
          this.exportError(num);
        });
    },
    // 正常导出
    exportNormal(res, num, currentPage, options, url) {
      if (res.code != 0) {
        res.data.list = [];
      }
      if (this.sbc === 'sendMailFeeOrPayFeeDetail') {
        res.data.list = this.calculation(res.data.list);
      }
      if (this.sbc === 'sendReceiveFeeSummary') {
        res.data.list = this.ischeckrow(res.data.list);
      }
      this.exportObj = {
        ...this.exportObj,
        sum: num, //一共获取数据的总次数
        current: currentPage, //当前获取第几次数据
        excelData: res.data.list,
      };
      // 递归 尾递归
      this.exportCsv(this);
      this.exportSync(currentPage, num, options, url);
    },
    // 出错的导出
    exportError(num) {
      this.btnDisabled = false;
      this.msg('网络出错，导出部分数据');
      this.exportObj = {
        ...this.exportObj,
        sum: num, //一共获取数据的总次数
        current: num, //当前获取第几次数据
      };
      this.exportObj.excelData = [];
      this.exportCsv(this);
    },
    // 导出当前页方法
    checklistExport() {
      this.btnDisabled = true;
      if (this.checklistData == '') {
        this.msg('没有数据无法导出');
        this.btnDisabled = false;
        return;
      }
      if (this.checklistData.length > 100000) {
        this.msg('数据量太大，请优化查询条件');
        this.btnDisabled = false;
        return;
      }
      this.exportObj = {
        ...this.exportObj,
        sum: 1, //一共获取数据的总次数
        current: 1, //当前获取第几次数据
        excelData: this.checklistData,
      };
      this.exportCsv(this);
      this.btnDisabled = false;
    },
    // 清除详细下载的按钮固定
    clearBtnDisabled() {
      this.btnDisabled = false;
    },
    msg(val, type = 'warning') {
      this.$message({
        showClose: true,
        message: val,
        type: type,
        duration: 3000,
      });
    },
      formatterFunc(arr, cellValue) {
        //表格formatter通用filter
          const result = arr.filter(item => item.value === cellValue)
          return result.length > 0 ? result[0].label : '-'
      }
  },
};

/**
 * 使用本方法需要页面自带csv组件，并且head需要写好
 */
/**
 * @param paramIn 传进来的查询参数 必须
 * @param totalRow 查询的数据的总条数  必须
 * @param type String类型， day表示按天导出，page表示安装按页导出 必须
 * @param fileName csv文件名 必须
 * @param url  请求的url 必须
 * @param methodType 请求方法 必须 
 * @param startDateKeyStr 开始日期的键 必须 按页和按单号导出不需要
 * @param endDateKeyStr   结束日期的键 必须 按页和按单号导出不需要
 * @param isBillCode 是否按照单号导出 可选
 * @param checklistData 导出的数据，如果是单页导出 可选
 * @param queryInternal   查询时间间隔，可手动设定（以天为单位） 
 * @param pageSize 导出每页的条数,默认为5000 必须
 * 
 * exportBasicUtils 调用这个方法
 */
/**
 * 示列
 *   let paramObj = {
      paramIn: param,
      startDateKey: "startDate",
      endDateKey: "endDate",
      methodType: "post", 
      url: getDetailUrl,
      fileName: "客户账单明细",
      totalRow: this.currRow.totalTicket,
      queryInternal: 10,
      allEndDate: end,
      type: "day"
    };
    this.exportBasicUtils(paramObj);
 */
export const exportCsvUtils = {
  data() {
    return {
      // 导出
      btnDisabled: false, // 控制是否能点击按钮
      exportObj: {
        visible: false,
        sum: 1, //一共获取数据的总次数
        current: 0, //当前获取第几次数据
        fileName: '导出文件名',
        excelData: [],
      },
    };
  },
  methods: {
    exportBasicUtils(paramObj, pageSize = 5000) {
      // 锁定按钮 10秒之后释放
      this.btnDisabled = true;
      setTimeout(() => {
        this.btnDisabled = false;
      }, 3000);
      const {
        totalRow,
        isBillCode,
        checklistData,
        fileName,
        type
      } = paramObj;
      // 避免多次赋值
      this.exportObj.fileName = fileName;
      if (!totalRow) {
        this.msg('暂无数据，无法导出');
        return;
      }
      if (totalRow > 200000) {
        this.msg('您查询到的数据量过大（最多20万条），请细化查询条件后再进行导出');
        return;
      }
      // 如果是单号查询和总条数小于等于当前数据条数据是，直接导出
      if (isBillCode || (checklistData && checklistData.length >= totalRow)) {
        if (checklistData.length > 0) {
          this.exportObj = {
            fileName: fileName,
            sum: 1, //一共获取数据的总次数
            current: 1, //当前获取第几次数据
            excelData: checklistData,
            visible: true
          };
        } else {
          this.msg('暂无数据，无法导出');
        }
        return;
      }
      if (type == 'day') {
        this.exportObj = {
          excelData: [],
          fileName: fileName,
          sum: totalRow, //一共获取数据的总次数
          current: 1, //当前获取第几次数据         
        };
        this.finished(paramObj, paramObj.paramIn, 0);
      } else {
        // 一天天导 两个递归 尾递归 正常导出
        // 一页页导
        let num = Math.ceil(totalRow / pageSize);
        // 需要导出多少次
        this.exportObj = {
          excelData: [],
          fileName: fileName,
          sum: num, //一共获取数据的总次数
          current: 0, //当前获取第几次数据  
        };
        this.exportByPage(0, num, paramObj, pageSize);
      }
    },
    // 按页导出
    exportByPage(currentPage, num, paramObj, pageSize) {
      if (currentPage == num) {
        // 当页数等于总次数，则结束循环，跳出递归
        return;
      }
      const {
        methodType,
        url,
        paramIn
      } = paramObj;
      ++currentPage;
      let params = {
        ...paramIn,
        pageSize: pageSize,
        currentPage: currentPage,
      };
      axiosConfig(methodType, url, params, true, false)
        .then(result => {
          this.exportObj = {
            ...this.exportObj,
            sum: num,
            current: currentPage,
            excelData: result.data.list,
            visible: true,
          };
          return this.exportByPage(currentPage, num, paramObj, pageSize);
        })
        .catch(res => {
          this.msg('网络出错，导出部分数据');
          this.exportObj = {
            ...this.exportObj,
            sum: num, // 总条数
            current: num, //当前获取的数据量累加
            excelData: [],
            visible: true,
          };
        });
    },
    // 尾调用优化，v8引擎会自动优化
    callback(paramObj, param, currentTotal = 1) {
      const {
        methodType,
        url,
        totalRow
      } = paramObj;
      axiosConfig(methodType, url, param, true, false)
        .then(result => {
          // 如果当前页是最后一页则结束
          currentTotal = currentTotal + result.data.list.length;
          if (result.data.currentPage >= result.data.totalPage) {
            this.finished(paramObj, param, currentTotal);
          } else {
            param.currentPage = result.data.currentPage;
            param.currentPage = param.currentPage + 1;
            this.callback(paramObj, param, currentTotal);
          }
          this.exportObj = {
            ...this.exportObj,
            sum: totalRow, // 总条数
            current: currentTotal, //当前获取的数据量累加
            excelData: result.data.list,
            visible: true,
          };
        })
        .catch(res => {
          this.msg('网络出错，导出部分数据');
          this.exportObj = {
            ...this.exportObj,
            sum: totalRow, // 总条数
            current: totalRow, //当前获取的数据量累加
            excelData: [],
            visible: true,
          };
        });
    },
    // 时间处理 判断是否到了结束的时间0
    finished(paramObj, param, currentTotal = 0) {
      const {
        startDateKey,
        endDateKey,
        totalRow,
        fileName,
        allEndDate,
        queryInternal,
      } = paramObj;
      // 运行是时间加
      let interval = queryInternal ? queryInternal : 0;
      interval = interval > 0 ? interval - 1 : 0;
      let start = new Date(allEndDate).getTime();
      let end = new Date(param[endDateKey]).getTime();
      // 如果结束时间等于最终结束时间，且最终结束时间大于开始时间
      if (start == end && start > new Date(param[startDateKey]).getTime()) {
        param[startDateKey] = param[startDateKey];
      } else {
        param[startDateKey] = cloneDeep(
          this.addSeconds(param[endDateKey], 1)
        );
      }
      param[endDateKey] = cloneDeep(
        this.addSeconds(param[startDateKey], interval)
      );
      //如果结束时间大于原始结束时间，则将结束时间置为原始结束时间
      if (
        new Date(allEndDate).getTime() <
        new Date(param[endDateKey]).getTime() ||
        totalRow <= 5000
      ) {
        param[endDateKey] = allEndDate;
      }
      // 如果开始时间大于原始结束时间，则程序结束,导出数据
      if (
        new Date(allEndDate).getTime() <
        new Date(param[startDateKey]).getTime()
      ) {
        // 解决导出总条数和查询出的总条数不一致是，需要将
        setTimeout(() => {
          this.exportObj = {
            ...this.exportObj,
            fileName: fileName,
            sum: totalRow, // 总条数
            current: totalRow, //当前获取的数据量累加
            excelData: [],
            visible: true,
          };
        }, 1500);
      } else {
        // 没有结束，则继续调用下一个
        param.currentPage = 1;
        this.callback(paramObj, param, currentTotal);
      }
    },
    addSeconds(date, num = 0) {
      // 时间加几天，默认原始时间
      return moment(date).add(num, 'days').format('YYYY-MM-DD');
    },
    msg(val, type = 'warning') {
      this.$message({
        showClose: true,
        message: val,
        type: type,
        duration: 3000,
      });
    },
  },
};
